export class Estudiante {
    _id = '';
    nombre = '';
    apellidos = '';
    clase = '';
    email = '';
    contrasena = '';
}

export class Alumnos {
    //Atributos
    estudiante: Estudiante;
    id_item = '';
    posicion = {};
    datos = {
        alegria: [],
        asco: [],
        miedo: [],
        sorpresa: [],
        tristeza: [],
        ira: [],
        pulsaciones: [],
        tiempo: [],
        distraido: [],
        concentrado: [],
        frustrado: [],
        motivado: [],
    };

    constructor(est, posicion, act) {
        this.estudiante = est;
        this.id_item = est._id + act._id;
        this.posicion = posicion;
        this.datos = {
            alegria: [],
            asco: [],
            miedo: [],
            sorpresa: [],
            tristeza: [],
            ira: [],
            pulsaciones: [],
            tiempo: [],
            distraido: [],
            concentrado: [],
            frustrado: [],
            motivado: [],
        };
    }

    setActividad(act) {
        this.id_item = this.estudiante._id + act._id;
    }
}